.profile {
  width: 96%;
  max-width: 360px;
  display: -ms-flexbox;
  display: flex;
  min-height: 110px;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 5px;
}
.profile-card {
  -ms-flex-pack: justify;
      justify-content: space-between;

  display: -ms-flexbox;

  display: flex;
}
.profile-pic {
  width: 95px;
  height: 95px;
  margin: 4px;
  -ms-flex-item-align: center;
      align-self: center;
}
.profile-value {
  width: 68%;
  margin: 2px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.profile-value span {
  margin: 1px;
}
.expanded-card {
  background-color: #ececec;
  position: relative;
  z-index: 1200;
  height: 150px;
  max-width: 360px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding: 18px;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.expanded-card span {
  margin: 4px;
}
.profile-expandable {
}
.profile span {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.profile span div {
  font-size: 15px;
  font-weight: bold;
  margin-right: 5px;
}



