.message-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-align: normal;
      align-items: normal;
  border-radius: 20px;
  overflow: hidden;
  height: 70px;

  -webkit-transition: all 0.3s ease;

  -o-transition: all 0.3s ease;

  transition: all 0.3s ease;
}

.message-card p {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.message-content {
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  border-radius: 20px;
}
.message-content p {
  word-wrap: break-word;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 90%;
}



