.dropdown-card {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-item-align: center;
      align-self: center;
  -ms-flex-align: normal;
      align-items: normal;
  border-radius: 20px;
  overflow: hidden;
}

.dropdown-card p {
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.dropdown-content {
  background-color: #fff;
  display: -ms-flexbox;
  display: flex;
  word-break: break-all;
  -ms-flex-direction: column;
      flex-direction: column;
  border-radius: 20px;
}
.dropdown-content p {
  word-wrap: break-word;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: 90%;
}
.dropdown-content-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-direction: row;
      flex-direction: row;
}



